// 遮罩
.mask2:hover::before {
    position: absolute;
    content: "";
    top: 0;
    left: 0;
    background-color: rgba(0,0,0,0.5);
    width: 100%;
    height: 100%;
    z-index: 100;
  }

  .card-mask-content{
    position: absolute;
    content: "";
    top: 0;
    left: 0;
    z-index: 120;
    border: 1px solid white;
    height: 90%;
  }